import { createRouter, createWebHashHistory } from 'vue-router'

// 路由级代码分割（懒加载）
const routes = [
  {
    path: '/',
    name: 'Home',
    component: () => import('@/views/HomePage.vue'),
    meta: { title: '首页' }
  },
  {
    path: '/Project',
    name: 'ProjCenter',
    component: () => import('@/views/ProjCenter.vue'),
    meta: { title: '项目' }
  },
  {
    path: '/Project/PKUAutoElective',
    redirect: '/AccessWarning',
    name: 'PKUAutoElective',
    component: () => import('@/views/ProjCenter.vue'),
    meta: { title: '北大刷课机' }
  },
  {
    path: '/Project/PKUAutoRun',
    redirect: '/AccessWarning',
    name: 'PKUAutoRun',
    component: () => import('@/views/ProjCenter.vue'),
    meta: { title: '北大自动跑步' }
  },
  {
    path: '/Project/Yanxx_ChineseLearning',
    name: 'Yanxx_ChineseLearning',
    component: () => import('@/views/project/Yanxx_ChineseLearning.vue'),
    meta: { title: '严小希-语文学习小助手' }
  },
  {
    path: '/Project/Yanxx_location',
    name: 'Yanxx_location',
    component: () => import('@/views/project/Yanxx_location.vue'),
    meta: { title: '严小希-地理定位小助手' }
  },{
    path: '/Project/CalendarSystem',
    name: 'CalendarSystem',
    component: () => import('@/views/project/CalendarSystem.vue'),
    meta: { title: '严小希-日程管理小助手' }
  },{
    path: '/Project/RenjuGame',
    name: 'RenjuGame',
    component: () => import('@/views/project/RenjuGame.vue'),
    meta: { title: 'Renju 五子棋' }
  },
  {
    path: '/AccessWarning',
    name: 'AccessWarning',
    component: () => import('@/views/AccessWarning.vue'),
    meta: { title: '权限不足警告' }
  },
  {
    path: '/ContactAdmin',
    name: 'ContactAdmin',
    component: () => import('@/views/ContactAdmin.vue'),
    meta: { title: '联系管理员' }
  },
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router